import React, { useState, useCallback, useEffect } from "react";
import { Box, Typography, Grid, Paper, IconButton, Button, TextField } from "@mui/material";
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import Header from "./header/Header";
import MenuBar from "./menu/MenuBar";
import { getToken } from "../services/localStorageService";

const getWeeksOfMonth = (year, month) => {
  const firstDayOfMonth = new Date(year, month, 1);
  const lastDayOfMonth = new Date(year, month + 1, 0);
  const weeks = [];
  let week = [];

  for (let day = 1; day <= lastDayOfMonth.getDate(); day++) {
    const date = new Date(year, month, day);
    const dayOfWeek = date.getDay();

    week.push(date);

    if (dayOfWeek === 6 || day === lastDayOfMonth.getDate()) {
      weeks.push(week);
      week = [];
    }
  }

  return weeks;
};

const getWeekIndexOfDate = (weeks, date) => {
  for (let i = 0; i < weeks.length; i++) {
    if (weeks[i].some(d => d.toDateString() === date.toDateString())) {
      return i;
    }
  }
  return -1;
};

const WeekDays = () => {
  const today = new Date();
  const year = today.getFullYear();
  const month = today.getMonth();
  const weeks = getWeeksOfMonth(year, month);

  const initialWeekIndex = getWeekIndexOfDate(weeks, today);
  const [currentWeekIndex, setCurrentWeekIndex] = useState(initialWeekIndex);
  const [selectedDate, setSelectedDate] = useState(null);
  const [workingtime, setWorkingtime] = useState("");
  const [note, setNote] = useState("");
  const [type, setType] = useState("");
  const [project, setProject] = useState("");
  const [task, setTask] = useState("");
  const [userId, setUserId] = useState(null);

  const handlePreviousWeek = useCallback(() => {
    setCurrentWeekIndex((prevIndex) => Math.max(prevIndex - 1, 0));
  }, []);

  const handleNextWeek = useCallback(() => {
    setCurrentWeekIndex((prevIndex) => Math.min(prevIndex + 1, weeks.length - 1));
  }, [weeks.length]);

  const handleDateClick = (date) => {
    setSelectedDate(date);
  };

  const handleCancel = () => {
    setSelectedDate(null);
  };

  const handleSubmit = async () => {
    if (!userId) return; // Ensure userId is available

    const requestBody = {
      day: selectedDate.toISOString().split('T')[0],
      workingtime: parseFloat(workingtime),
      note,
      type,
      user: {
        id: userId
      },
      project: {
        name: project
      },
      task: {
        name: task
      }
    };

    try {
      const response = await fetch(`http://localhost:8080/timesheet`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${getToken()}`,
        },
        body: JSON.stringify(requestBody)
      });

      if (response.ok) {
        console.error("ok");
        handleCancel(); // n form sau khi gi thnh cng
      } else {
        console.error("Failed to submit form");
      }
    } catch (error) {
      console.error("Error submitting form:", error);
    }
  };

  useEffect(() => {
    const fetchUserDetails = async () => {
      const accessToken = getToken();
      if (!accessToken) return;

      try {
        const response = await fetch("http://localhost:8080/users/my-info", {
          method: "GET",
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        });

        if (response.ok) {
          const data = await response.json();
          setUserId(data.result.id);
        } else {
          console.error("Failed to fetch user details");
        }
      } catch (error) {
        console.error("Error fetching user details:", error);
      }
    };

    fetchUserDetails();
  }, []);

  return (
    <>
      <Header />
      <Box
        display="flex"
        flexDirection="row"
        height="100vh"
        bgcolor="#f0f2f5"
        marginTop={6}
      >
        <MenuBar />
        <Box p={3} flex={1}>
          <Typography variant="h6" gutterBottom>
            {today.toLocaleDateString('en-US', { month: 'long', year: 'numeric' })}
          </Typography>
          <Box display="flex" justifyContent="space-between" alignItems="center">
            <IconButton onClick={handlePreviousWeek} disabled={currentWeekIndex === 0}>
              <ArrowBackIosIcon />
            </IconButton>
            <Typography variant="h6">
              Week {currentWeekIndex + 1}
            </Typography>
            <IconButton onClick={handleNextWeek} disabled={currentWeekIndex === weeks.length - 1}>
              <ArrowForwardIosIcon />
            </IconButton>
          </Box>
          <Grid container spacing={2} justifyContent="center">
            {weeks[currentWeekIndex].map((date, idx) => (
              <Grid item xs={12} sm={1} key={idx}>
                <Paper
                  elevation={3}
                  sx={{
                    padding: 2,
                    textAlign: "center",
                    backgroundColor: date.toDateString() === today.toDateString() ? "#ff5722" : "#3f51b5",
                    color: "white",
                    cursor: "pointer",
                  }}
                  onClick={() => handleDateClick(date)}
                >
                  <Typography variant="h6">{date.getDate()}</Typography>
                  <Typography variant="body1">{date.toLocaleDateString('en-US', { weekday: 'short' })}</Typography>
                </Paper>
              </Grid>
            ))}
          </Grid>
          {selectedDate && (
            <Box 
              mt={4}
              p={2}
              bgcolor="white"
              borderRadius={1}
              boxShadow={3}
              maxWidth={400}
              mx="auto"
            >
              <Typography id="form-title" variant="h6" component="h2">
                Selected Date: {selectedDate?.toLocaleDateString()}
              </Typography>
              <Box mt={2}>
                <TextField
                  fullWidth
                  label="Working Time"
                  value={workingtime}
                  onChange={(e) => setWorkingtime(e.target.value)}
                  margin="normal"
                  size="small"
                />
                <TextField
                  fullWidth
                  label="Note"
                  value={note}
                  onChange={(e) => setNote(e.target.value)}
                  margin="normal"
                  size="small"
                />
                <TextField
                  fullWidth
                  label="Type"
                  value={type}
                  onChange={(e) => setType(e.target.value)}
                  margin="normal"
                  size="small"
                />
                <TextField
                  fullWidth
                  label="Project Name"
                  value={project}
                  onChange={(e) => setProject(e.target.value)}
                  margin="normal"
                  size="small"
                />
                <TextField
                  fullWidth
                  label="Task Name"
                  value={task}
                  onChange={(e) => setTask(e.target.value)}
                  margin="normal"
                  size="small"
                />
                <Box display="flex" justifyContent="space-between" mt={2}>
                  <Button 
                    variant="contained" 
                    color="primary" 
                    onClick={handleSubmit}
                  >
                    Submit
                  </Button>
                  <Button 
                    variant="outlined" 
                    color="secondary" 
                    onClick={handleCancel}
                  >
                    Cancel
                  </Button>
                </Box>
              </Box>
            </Box>
          )}
        </Box>
      </Box>
    </>
  );
};

export default WeekDays;
