import React, { useEffect, useState, useCallback } from "react";
import { debounce } from "lodash";
import { getToken } from "../services/localStorageService";
import Header from "./header/Header";
import MenuBar from "./menu/MenuBar";
import {
  Table,
  Paper,
  TableContainer,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Box,
  TextField,
} from "@mui/material";

const TableComponent = () => {
  const [data, setData] = useState([]);

  const fetchData = async (search = "") => {
    try {
      const url ="http://localhost:8080/leavetype";
      
      const response = await fetch(url, {
        method: "GET",
        headers: {
          Authorization: `Bearer ${getToken()}`, // Set Authorization header
        },
      });

      if (!response.ok) {
        throw new Error('Network response was not ok');
      }

      const result = await response.json();
      setData(result);
    } catch (error) {
      console.error('There was a problem with the fetch operation:', error);
    }
  };

  useEffect(() => {
    fetchData();
  }, []); // Run once after component mounts

  
  return (
    <>
      <Header />
      <Box
        display="flex"
        flexDirection="row"
        height="100vh"
        bgcolor="#f0f2f5"
        marginTop={6}
      >
        <MenuBar />
        <Box flex={1} p={2}>
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }}>
              <TableHead>
                <TableRow>  
                  <TableCell>STT</TableCell>
                  <TableCell>Name</TableCell>
                  <TableCell>So Ngay Phep</TableCell>
                  <TableCell>Loai</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {data.map((item, index) => (
                  <TableRow key={index}>
                    <TableCell>{index}</TableCell>
                    <TableCell>{item.name}</TableCell>
                    <TableCell>{item.songayphep || 'N/A'}</TableCell>
                    <TableCell>{item.loai || 'N/A'}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
      </Box>
    </>
  );
};

export default TableComponent;
